body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.weather-app {
  background-color: #f1ebcb;
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.weather-main {
  background-color: #fff;
  width: 60%;
  padding: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  color: rgb(4, 4, 4);
}
.weather {
  width: 100%;
}
.weather-info {
  padding-top: 20px;
}
.weather-footer {
  padding-top: 20px;
}

.link {
  display: flex;
  text-decoration: none;
  color: #000;
  padding-bottom: 20px;
  text-align: center;
  /* align-self: center; */
}
.temp-description {
  display: flex;
  justify-content: left;
  grid-gap: 20px;
  gap: 20px;
}
.weather-list {
  list-style: none;
}
.weather-forecast {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
}
.temp-min {
  opacity: 0.6;
  margin-left: 5px;
}
ul {
  -webkit-padding-start: 0;
          padding-inline-start: 0;
}
.descr-right {
  font-size: 50px;
}

.descr-left {
  padding-top: 8px;
}

@media screen and (max-width: 800px) {

  .weather-app {
    background-color: white;
  }
  .weather-main {
    width: 100%;
  }
}  

@media screen and (max-width: 440px) {

  .btn.btn-dark.w-100{
    display: none;
  }

  .weather-forecast{
    display: none;
  }
}
